.showroom-main{
  padding-bottom: 26.6875em;
  overflow: hidden;
  .faq-main__img2{
    bottom: unset;
    right: unset;
    top: 10.88em;
    left: 6.69em;
  }
  &__img{
    position: absolute;
    top: -.59em;
    right: -9.75em;
    transform: rotate(-165deg);
    width: 45em;
  }
}

.showroom{
  position: relative;
  z-index: 3;
  padding-bottom: 2.5em;
  .invent-slider{
    margin: -14.5em auto 0;
    width: 53.625em;
    max-width: 100%;
  }
  &__content{
    margin: 5em auto 0;
    width: 39.875em;
    max-width: 100%;
    .text38{
      color: $black;
    }
  }
  &__text{
    margin-top: 1em;
    @include grid100gap(1em);
    p{
      line-height: inherit;
    }
  }
}

.showroom-info{
  padding: 2.5em 0 3.75em;
  &__grid{
    display: grid;
    grid-template-columns: 30.75em calc(100% - 32.25em);
    gap: 1.5em;
  }
  .showroom__content{
    margin-top: 2.5em;
  }
  .invent-slider{
    margin: 5em auto 0;
    width: 53.625em;
    max-width: 100%;
  }
}

.functions{
  grid-row: 1/3;
  padding-top: .625em;
  padding-bottom: 5em;
  background-color: $black2;
  border-radius: 1.875em;
  overflow: hidden;
  &__rows{
    @include grid100gap(.5em);
  }
  &__row{
    display: flex;
    justify-content: center;
  }
  &__text{
    margin-top: 2.94em;
    text-align: center;
    color: #fff;
  }
  &__title{
    background: linear-gradient(1deg, #9186E7 0%, #7DC0FB 22.92%, #C7F8FF 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: 700;
    font-size: 2.5em;
    line-height: 115%;
  }
}

.functions-item{
  flex-shrink: 0;
  margin: 0 .25em;
  padding: 0 1.125em;
  border-radius: 1.875em;
  background: linear-gradient(225deg, rgba(255, 255, 255, 0.10) 0%, rgba(255, 255, 255, 0.10) 50%, rgba(255, 255, 255, 0.10) 100%);
  color: #fff;
  font-weight: 600;
  height: 3em;
  @include center;
  border: 1px solid rgba(#fff,.18);
  span{
    font-size: 1.0625em;
    line-height: 120%;
  }
}

.scenario{
  padding: 2em 1em 1.25em;
  border-radius: 1.25em;
  background: #FFF;
  overflow: hidden;
  &__top{
    text-align: center;
    margin-bottom: 1.25em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1em;
  }
  &__download{
    display: flex;
    align-items: center;
    img{
      width: 1.25em;
      flex-shrink: 0;
      margin-right: .25em;
    }
  }
}

.scenario-feed{
  margin-top: .75em;
  .swiper{
    &-wrapper{
      transition-timing-function: linear !important;
    }
    &-slide{
      padding-right: .75em;
      width: 5.75em;
    }
  }
}

.scenario-card{
  height: 5em;
  border-radius: 1.25em;
  background: #FFF;
  box-shadow: 0 .25em 3.0625em 0 rgba(0, 0, 0, 0.13), 0 0 .75em 0 rgba(164, 164, 164, 0.32) inset;
  @include center;
  img{
    max-width: 100%;
    max-height: 100%;
    border-radius: inherit;
  }
}

.trifle{
  border-radius: 1.875em;
  background: linear-gradient(180deg, #F4FCFF 0%, rgba(244, 252, 255, 0.00) 100%), #FFF;
  padding: 0 1.25em 1.25em;
  display: flex;
  flex-direction: column;
  position: relative;
  &__top{
    position: relative;
  }
  &__rounds{
    width: 17.5em;
    display: block;
    margin: 0 auto;
  }
  &__text{
    padding-top: 1em;
    margin: auto auto 0;
    width: 25em;
    max-width: 100%;
    @include grid100gap(.75em);
    text-align: center;
    .text17{
      max-width: 18em;
      margin: 0 auto;
    }
  }
  &__icon{
    position: absolute;
    background-color: $black2;
    border-radius: 50%;
    width: 2em;
    height: 2em;
    @include center;
    z-index: 3;
    img{
      width: 1.5em;
      display: block;
      transform: scale(0);
    }
  }
  &__icon1{
    top: 3em;
    right: 13.81em;
  }
  &__icon2{
    top: 8.5em;
    left: 14.5em;
  }
  &__messages{
    position: absolute;
    padding: .875em 3em 0 2em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    z-index: 2;
    width: 100%;
    top: 0;
    left: 0;
  }
  .message{
    background: linear-gradient(135deg, #D6A1FF 0%, #FFD4D4 33.33%, #FFD4E6 66.67%, #DEB2FA 100%);
    width: 18.5em;
    max-width: 100%;
    border-radius: 1.875em 1.875em 0 1.875em;
    margin-bottom: 2.38em;
    margin-right: 0 !important;
    font-weight: 500;
    opacity: 0;
    position: relative;
    &:nth-child(even){
      border-radius: 1.875em 1.875em 1.875em 0;
      margin-left: auto;
      width: 16em;
      &:before{
        content: '';
        position: absolute;
        background: linear-gradient(180deg, rgba(248, 253, 255, 0.00) 0%, #F8FDFF 100%);
        left: 0;
        top: 0;
        border-radius: inherit;
        z-index: 2;
        width: 100%;
        height: 100%;
      }
    }
    .text17{
      line-height: 88%;
    }
  }
}

.brands{
  margin-top: 1.5em;
  padding: 3.75em;
  min-height: 21.375em;
  border-radius: 1.875em;
  background: #FBFEFF;
  position: relative;
  &__content{
    @include grid100gap(.625em);
    max-width: 32em;
    position: relative;
    z-index: 2;
  }
  .text40{
    color: #DA6B71;
  }
  &__desc{
    margin-top: .75em;
    max-width: 27em;
    font-weight: 400;
  }
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    img{
      height: 100%;
      border-radius: 0 1.875em 1.875em 0;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}

.showroom-visit{
  padding: 3.75em 0;
  &__top{
    margin: 0 auto;
    max-width: 26em;
    text-align: center;
  }
  &__items{
    @include grid100gap(5em);
    margin: 2.5em auto 0;
    max-width: 53.25em;
    width: 100%;
  }
  .calc-btn{
    width: 14.5625em;
    text-align: left;
    white-space: nowrap;
    &__wrap{
      margin-top: 2em;
      justify-content: center;
    }
  }
}

.visit-card{
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: .625em;
    }
  }
  &__content{
    width: 40em;
    max-width: 100%;
    margin: 2em auto 0;
    @include grid100gap(1.25em);
    .text24{
      text-align: center;
      max-width: 15.8em;
      margin: 0 auto;
    }
  }
}

.types{
  display: flex;
  flex-wrap: wrap;
  width: calc(100% + 1.5em);
  margin-top: 1.5em;
}

.type-card{
  padding: 3.75em;
  border-radius: 1.875em;
  width: calc(55% - 1.5em);
  background: linear-gradient(317deg, rgba(88, 227, 101, 0.30) 0%, rgba(213, 255, 143, 0.30) 67.19%, rgba(247, 255, 109, 0.30) 100%);
  margin-right: 1.5em;
  min-height: 21.375em;
  position: relative;
  overflow: hidden;
  &._small{
    width: calc(45% - 1.5em);
    .type-card__img{
      width: 17.5em;
    }
  }
  &._red{
    background: linear-gradient(180deg, rgba(249, 197, 171, 0.50) 0%, rgba(255, 250, 234, 0.50) 100%);
    .text40{
      color: #EF804B;
    }
  }
  .text40{
    color: #477973;
  }
  &__img{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 23.75em;
  }
  &__content{
    @include grid100gap(.75em);
    max-width: 27em;
    position: relative;
    z-index: 2;
    .text17{
      max-width: 23em;
    }
  }
}


@media screen and (max-width: 992px){
  .showroom-main{
    padding-bottom: 24em;
    .faq-main__img2{
      top: .75em;
      left: 1.25em;
    }
    &__img{
      right: -1.36em;
      bottom: 9.5em;
      top: unset;
      width: 10.75em;
    }
  }

  .showroom{
    .invent-slider{
      margin: -7.87em auto 0;
    }
    &__content{
      margin-top: 2.5em;
    }
    &__text{
      margin-top: .8em;
    }
  }

  .showroom-info{
    padding-bottom: 2.5em;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    .showroom__content{
      margin-top: 1.25em;
    }
    .invent-slider{
      margin-top: 2.5em;
    }
  }

  .functions{
    padding-bottom: 4em;
    &__text{
      margin-top: 2.5em;
    }
    &__title{
      font-size: 1.875em;
    }
  }

  .scenario{
    &__download{
      text-decoration: underline;
      font-weight: 400;
      img{
        display: none;
      }
    }
  }

  .brands{
    padding: 1.25em;
    min-height: 15em;
    margin-top: 1.25em;
    &__content{
      gap: 0;
      max-width: 24em;
    }
    &__desc{
      margin-top: .5em;
    }
  }

  .showroom-visit{
    padding: 2.5em 0;
    &__items{
      gap: 4em;
    }
  }

  .visit-card{
    &__content{
      gap: .75em;
    }
  }

  .types{
    @include grid100gap(1.25em);
    margin-top: 1.25em;
    width: 100%;
  }

  .type-card{
    width: 100% !important;
    margin: 0;
    padding: 1.25em 1.875em;
    &__content{
      gap: .5em;
      .text17{
        font-weight: 400;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .functions-item{
    height: 2.25em;
    padding: 0 1em;
    span{
      font-size: .875em;
    }
  }

  .trifle{
    padding: 0 1.5em 1.5em;
    &__text{
      padding-top: .25em;
      gap: .5em;
      .text28{
        font-weight: 700;
      }
    }
    &__icon1{
      right: 5.25em;
      top: 4em;
    }
    &__icon2{
      left: 8em;
    }
    &__messages{
      position: static;
      margin-top: .5em;
      padding: 0;
    }
    .message{
      margin-bottom: 1.25em;
      border-radius: 1.875em !important;
      &:before{
        display: none;
      }
    }
  }

  .brands{
    padding-bottom: 18.75em;
    text-align: center;
    &__img{
      top: unset;
      bottom: 0;
      height: unset;
      width: 100%;
      img{
        width: 100%;
        height: auto;
        border-radius: 0 0 1.875em 1.875em;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .showroom-visit{
    .container{
      padding: 0;
    }
  }

  .visit-card{
    &__content{
      padding: 0 .625em;
    }
  }

  .type-card{
    text-align: center;
    &__content{
      .text17{
        margin: 0 auto;
      }
    }
  }
}
