.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;

    &__bg {
        background: rgba($black, 0.7);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: $gray2;
        backdrop-filter: blur(11px);
        width: 26.5em;
        max-width: calc(100% - 1em);
        position: relative;
        z-index: 3;
        border-radius: 1.25em;
    }

    &__scroll {
        overflow-y: auto;
        max-height: calc(100 * var(--vh));
        padding: 5.25em 2em 2em;
    }

    &__close {
        position: absolute;
        right: 2em;
        top: 2em;
        width: 1.75em;
        background-color: transparent;
        svg {
            width: 100%;
            height: auto;
        }
        &:hover{
            line{
                fill: $black2;
            }
        }
    }
}

.modal-form{
    margin-top: 1.5em;
    @include grid100gap(1.25em);
    &__hint{
        margin-top: .25em;
    }
}


@media screen and (max-width: 992px){
    .modal{
        &__scroll{
            padding-left: 1.25em;
            padding-right: 1.25em;
        }
        &__info{
            .text24{
                font-size: 1.25em;
            }
            .text16{
                line-height: 140%;
                font-size: .875em;
            }
        }
        .consult-form{
            gap: 1em;
            margin-top: 0;
        }
    }
}
