.main2{
  @media screen and (min-width: 993px){
    margin-top: -5em;
    padding-top: 6.25em;
    padding-bottom: 21.25em;
  }
  &:before{
    max-height: 100%;
  }
  .info-block{
    margin-top: 6em;
    gap: 1.25em;
    max-width: 40em;
    &__top,
    &__nav{
      display: flex;
      justify-content: center;
    }
  }
  .calc-btn__wrap{
    justify-content: center;
    margin-top: 1.25em;
  }
}

.error404{
  flex-grow: 1;
  &__btn{
    padding: 0 4.16em;
    position: relative;
    img{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 2.12em;
      width: .375em;
    }
  }
}




@media screen and (max-width: 992px){
  .main2{
    min-height: 45em;
    &:before{
      background-size: cover;
      background-position: center top;
    }
    .info-block{
      gap: .75em;
      max-width: 20em;
      margin-top: 0;
      &__top{
        margin-bottom: .5em;
      }
      &__nav{
        margin-top: .5em;
      }
    }
  }
}
