.medium-text {
  font-weight: 500;
}

.desc {
  p {
    &:not(:first-child) {
      margin-top: 1.25em;
    }
  }
}

.equipment-main {
  padding-bottom: 29.5em;
  &__img {
    position: absolute;
    top: 7em;
    right: 9.375em;
    width: 8.75em;
  }
  &__img2 {
    position: absolute;
    left: 0;
    bottom: 17.5em;
    width: 25em;
  }
}

.safe-equipment {
  padding: 0 0 2.5em 0;
  margin-top: -15em;
  &__img {
    max-width: 53.25em;
    margin: 0 auto;
    img {
      width: 100%;
    }
  }
  &__desc {
    max-width: 40em;
    margin: 2.5em auto 0;
  }
}

.advantages-equipment {
  padding: 2.5em 0;
  &__desc {
    max-width: 40em;
    margin: 0 auto;
  }
  &__subtitle {
    margin: 2em 0 1em 0;
  }
  &__link {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 0.0625em solid rgba(0, 0, 0, 0.20);
    border-radius: 3.75em;
    padding: 1em 1.5em;
    margin-top: 5em;
    &:hover {
      border-color: #fc891c;
      svg path {
        fill: #fc891c;
      }
    }
    i {
      flex-shrink: 0;
      margin-left: 1.25em;
    }
  }
}

.advantages2-equipment {
  padding: 2.5em 0;
  &__desc {
    max-width: 40em;
    margin: 0 auto;
  }
  &__text {
    margin-top: 1em;
  }
  &__quote {
    max-width: 53.75em;
    margin: 5em auto 0;
    display: flex;
    border-radius: 0.625em;
    background: #E8E8E8;
    padding: 2em;
    &-icon {
      flex-shrink: 0;
      padding-right: 1.25em;
      span {
        font-size: 4.25em;
        line-height: 90%;
        font-weight: 700;
        color: #BABABA;
      }
    }
    &-desc {
      display: grid;
      grid-template-columns: 100%;
      gap: 1em;
      padding-left: 1.25em;
      border-left: 0.0625em solid #BABABA;
    }
    &-text {
      font-size: 1.25em;
      line-height: 123%;
      font-weight: 700;
    }
    &-name {
      font-size: 1.0625em;
      line-height: 120%;
      font-weight: 500;
    }
  }
}

.advantages3-equipment {
  padding: 2.5em 0;
  &__inner {
    max-width: 40em;
    margin: 0 auto;
  }
  &__text {
    margin-top: 1em;
  }
}

.advantages4-equipment {
  padding: 2.5em 0;
  &__inner {
    max-width: 40em;
    margin: 0 auto;
  }
  &__text {
    margin-top: 1em;
  }
}

.advantages5-equipment {
  padding: 2.5em 0;
  &__inner {
    background: #E8E8E8;
    padding: 2.5em 0;
  }
  &__desc {
    max-width: 40em;
    margin: 0 auto;
  }
  &__text {
    margin-top: 1em;
  }
}

.advantages6-equipment {
  padding: 2.5em 0;
  &__desc {
    max-width: 41.25em;
    margin: 0 auto;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
    margin-top: 1em;
    li {
      position: relative;
      padding-left: 1.125em;
      &:after {
        content: '';
        position: absolute;
        top: 0.3125em;
        left: 0;
        width: 0.75em;
        height: 0.75em;
        background: #BABABA;
        border-radius: 50%;
      }
    }
  }
  &__list2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
    margin-top: 1em;
    li {
      list-style: auto;
      list-style-position: inside;
      &::marker {
        font-size: 1.25em;
        line-height: 120%;
        font-weight: 500;
        color: #BABABA;
      }
      .advantages6-equipment__subtitle {
        display: inline-block;
      }
    }
  }
  &__subtitle {
    font-size: 1.25em;
    line-height: 120%;
    font-weight: 500;
    color: #2A2A2A;
  }
  &__text {
    margin-top: 0.5em;
    p {
      font-size: 1.0625em;
      line-height: 120%;
      font-weight: 500;
    }
  }
  &__slider {
    max-width: 53.125em;
    margin: 5em auto 0;
    position: relative;
    overflow: hidden;
    .swiper-pagination {
      position: absolute;
      bottom: 1.5em;
      left: 0;
      width: 100%;
      justify-content: center;
    }
  }
}

.features-equipment {
  padding: 2.5em 0;
  &__inner {
    background: linear-gradient(306deg, rgba(255, 153, 0, 0.00) 0%, rgba(255, 153, 0, 0.30) 100%), linear-gradient(156deg, rgba(0, 240, 255, 0.00) 0%, rgba(0, 178, 255, 0.30) 100%), #E8E8E8;
    border-radius: 1.0625em;
    position: relative;
    padding: 2.5em 6.94em;
    max-width: 66.875em;
    margin: 0 auto;
    overflow: hidden;
  }
  &__img {
    position: absolute;
    top: 1.5em;
    right: 2.25em;
    width: 8.75em;
  }
  &__img2{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 9em;
  }
  &__img3{
    display: none;
  }
  &__desc {
    max-width: 30.625em;
  }
  &__subtitle {
    margin-top: 1em;
  }
  &__items {
    max-width: 50em;
    display: flex;
    flex-wrap: wrap;
    margin-top: 2em;
  }
  &__item {
    display: flex;
    align-items: center;
    background: rgba(255, 255, 255, 0.40);
    border-radius: 1em;
    padding: 1.5625em 1.25em 1.5625em 0.625em;
    margin-bottom: 0.75em;
    &:not(:last-child) {
      margin-right: 0.75em;
    }
    &-desc {
      margin: 0 0.75em;
      span {
        font-size: 1.25em;
        line-height: 120%;
        font-weight: 500;
      }
    }
    &-icon {
      flex-shrink: 0;
      width: 1em;
      img {
        width: 100%;
      }
    }
    &-icon2 {
      flex-shrink: 0;
      width: 0.625em;
      img {
        width: 100%;
      }
    }
  }
}

.examples{
  padding: 3.75em 0;
  @media screen and (min-width: 993px){
    .automation__items{
      grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
      gap: 1.25em;
    }
  }
  .automation__items{
    border-color: $gray;
  }
  .automation-item{
    &:before{
      background: $gray;
    }
  }
  .automation-card{
    &__item{
      background: rgba(255, 255, 255, 0.60);
      min-width: unset;
    }
    &__desc{
      color: #3F3F3F;
    }
    &._blue{
      background: linear-gradient(0deg, rgba(134, 211, 255, 0.20) 0%, rgba(134, 211, 255, 0.20) 100%), rgba(255, 255, 255, 0.30);
      backdrop-filter: blur(11px);
      .automation-card__item{
        background: linear-gradient(0deg, rgba(134, 211, 255, 0.40) 0%, rgba(134, 211, 255, 0.40) 100%), rgba(255, 255, 255, 0.80);
      }
    }
  }
}



@media screen and (max-width: 992px) {
  .text28-mob {
    font-size: 1.75em;
  }

  .text17-mob {
    font-size: 1.0625em;
  }

  .equipment-main {
    padding-bottom: 26.0625em;
    &__img {
      top: 0;
      right: 1.25em;
      width: 5em;
    }
    &__img2 {
      width: 13.75em;
    }
  }

  .safe-equipment {
    &__img {
      width: calc(100% + 1.25em);
      margin-left: -0.625em;
    }
  }

  .advantages-equipment {
    padding: 2.5em 0 1.25em 0;
    &__subtitle {
      margin: 1.25em 0 1em 0;
    }
    &__link {
      margin-top: 2.5em;
    }
  }

  .advantages2-equipment {
    padding: 1.25em 0 2.5em;
    &__quote {
      width: calc(100% + 1.25em);
      margin-left: -0.625em;
      padding: 2em 0.625em;
      &-icon {
        padding-right: 0.75em;
      }
      &-desc {
        padding-left: 0.75em;
      }
      &-text {
        font-size: 1.0625em;
        font-weight: 500;
      }
      &-name {
        font-size: 0.8125em;
      }
    }
  }

  .advantages5-equipment {
    .container {
      padding: 0;
    }
    &__inner {
      padding: 2.5em 0.625em;
    }
  }

  .advantages6-equipment {
    &__subtitle {
      font-size: 1.0625em;
    }
    &__text {
      p {
        font-size: 0.875em;
      }
    }
    &__slider {
      width: calc(100% + 1.25em);
      margin-left: -0.625em;
    }
  }

  .features-equipment {
    .container {
      padding: 0;
    }
    &__inner {
      padding: 2.5em 0.625em 1.75em;
      overflow: hidden;
    }
    &__img {
      top: -2.875em;
      right: 0;
    }
    &__img2{
      display: none;
    }
    &__img3{
      position: absolute;
      display: block;
      right: 0;
      bottom: 0;
      width: 11.25em;
    }
    &__item {
      padding: 1.125em 0.625em;
      &-desc {
        span {
          font-size: 1.0625em;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .equipment-main {
    &__img2 {
      bottom: 9.6875em;
    }
  }

  .safe-equipment {
    margin-top: -7.5em;
  }

  .features-equipment {
    &__inner {
      padding: 2.5em 0.625em;
    }
    &__items {
      display: grid;
      grid-template-columns: 100%;
      gap: 0.75em;
    }
    &__item {
      margin: 0;
      &:not(:last-child) {
        margin: 0;
      }
    }
  }
}
