.faq-main{
  &__img{
    position: absolute;
    width: 37.5em;
    bottom: 2.875em;
    left: .3125em;
    transform: rotate(15deg);
  }
  &__img2{
    position: absolute;
    width: 8.75em;
    bottom: 11.25em;
    right: 24.88em;
  }
}

.faq-card{
  display: grid;
  grid-template-columns: 50% 50%;
  &:not(:last-child){
    border-bottom: 1px solid rgba(42,42,42,.2);
  }
  &__img{
    min-height: 26.25em;
    @include center;
    img{
      max-width: 30em;
      max-height: 100%;
      display: block;
    }
  }
  &__content{
    padding: 3.75em 0;
  }
  @media screen and (min-width: 577px){
    &:nth-child(even){
      .faq-card__content{
        order: -1;
      }
    }
  }
  &__tabs{
    margin: 1.5em 0;
  }
}

._tabs-parent{
  &._second-active{
    .tabs__wrap:before{
      left: 50%;
    }
  }
}

.tabs{
  display: flex;
  &__wrap{
    display: flex;
    border-radius: 3.75em;
    background: $gray2;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: 50%;
      background-color: $black;
      border-radius: inherit;
      transition: all .3s;
    }
  }
}

.tab{
  width: 6.625em;
  height: 2.25em;
  @include center;
  font-weight: 600;
  transition: all .3s;
  cursor: pointer;
  border-radius: inherit;
  position: relative;
  z-index: 2;
  span{
    font-size: 0.8125em;
    line-height: 120%;
  }
  &._active{
    color: #fff;
  }
  &._active{
    pointer-events: none;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}


@media screen and (max-width: 992px){
  .faq-main{
    &__img{
      width: 15em;
      bottom: 4.38em;
      left: -.94em;
    }
    &__img2{
      width: 5em;
      bottom: 16em;
      right: 1.5em;
    }
  }

  .faq-card{
    &__content{
      padding: 1.25em .625em 2em;
    }
    &__tabs{
      margin: 1.25em 0;
    }
    .text17{
      font-weight: 400;
    }
    &__img{
      img{
        max-width: 100%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .faq{
    .container{
      padding: 0;
    }
  }

  .faq-card{
    grid-template-columns: 100%;
    &:not(._big){
      .faq-card__img img{
        width: 12.5em;
      }
    }
    &__img{
      min-height: 16.75em;
    }
  }
}
